import React, {FC, useEffect, useState} from 'react';
import RightBar from "./RightBar";
import LeftBar from "./LeftBar";
import {useAppSelector} from "../hooks/redux";
import axios from "axios";
import {BidsFilterDropDown} from "./CustomDropdown";


interface IBetsProps {
    handleChangeType: (value: string) => void;
    handleChangePeriod: (value: string) => void;
    bets: any;
    period: string;
}


const Bets: FC<IBetsProps> = ({handleChangeType, handleChangePeriod, bets, period}) => {
    const period_opts = [
        {value: 'all', label: ' ', className: 'frb-one-opt'},
        {value: 'hour', label: '   ', className: 'frb-one-opt'},
        {value: 'today', label: '  ', className: 'frb-one-opt'},
        {value: 'week', label: '   ', className: 'frb-one-opt'},
        {value: 'month', label: '   ', className: 'frb-one-opt'},
    ]
    const res = {
        'undefined': {
            name: ' ',
            style: '#CD7F32'
        },
        'win': {
            name: '',
            style: '#50C878'
        },
        'lose': {
            name: '',
            style: 'red'
        }
    }
    const [showDropdown, setShowDropdown] = useState(false)
    return (
        <div id="lk-mid">
            <div className="lk-table">
                <div className="lkt-filter">
                    <div className="lktf-bttns">
                        <div onClick={() => handleChangeType('bets')}
                             className="lktfb-one">
                        </div>
                        <div onClick={() => handleChangeType('operations')}
                             className="lktfb-one">
                        </div>
                    </div>
                    <div className="lktf-shedule">
                        <div className="lktfs-bttn" style={{display: 'flex', justifyContent: 'center'}}>
                            <div className="global-ico gi-shedule"/>

                            {/*<Dropdown*/}
                            {/*    arrowClosed={true}*/}
                            {/*    options={period_opts}*/}
                            {/*    placeholder={' '}*/}
                            {/*    // controlClassName={'frb-one'}*/}
                            {/*    // menuClassName={'frb-one-opts'}*/}
                            {/*    onChange={e => handleChangePeriod(e.value)}*/}
                            {/*/>*/}

                            <BidsFilterDropDown
                                title={' '}
                                showDropdown={showDropdown}
                                setShowDropdown={setShowDropdown}
                                items={period_opts}
                                handleChangePeriod={handleChangePeriod}
                                period={period}
                            />

                            {/*<div className="global-ico gi-arrow-bot-g"/>*/}
                        </div>
                    </div>
                </div>
                <div className="lkt-row lktr-title">
                    <div className="lktr-date"></div>
                    <div className="lktr-time"></div>
                    <div className="lktr-name"> </div>
                    <div className="lktr-type"> </div>
                    <div className="lktr-pari-name"> </div>
                    <div className="lktr-sum"></div>
                    <div className="lktr-kf"></div>
                    <div className="lktr-result"></div>
                </div>

                {
                    bets.map((bid: any) => (
                        <div className="lkt-row">
                            <div className="lktr-date">{bid[6].split(' ')[0]}</div>
                            <div className="lktr-time">{bid[6].split(' ')[1]}</div>
                            <div className="lktr-name">
                                {
                                    bid[0].split(' VS ')[0]
                                }
                                <br/>
                                {
                                    bid[0].split(' VS ')[1]
                                }
                            </div>
                            <div className="lktr-type">
                                {
                                    bid[1] === ' ( )'
                                    ? '' : ''
                                }
                            </div>
                            <div className="lktr-pari-name">{bid[2]}</div>
                            <div className="lktr-sum">{bid[3]}
                                <div className="global-ico gi-coin"/>
                            </div>
                            <div className="lktr-kf">{bid[4]}</div>
                            <div
                                style={{
                                    padding: 4,
                                    //@ts-ignore
                                    backgroundColor: res[bid[5]].style,
                                    borderRadius: 6,
                                    textAlign: 'center'
                                }}
                                className="lktr-result">{
                                //@ts-ignore
                                res[bid[5]].name
                            }
                            </div>
                        </div>
                    ))
                }
            </div>
        </div>
    )
}

const Operations: FC<IBetsProps> = ({handleChangeType, handleChangePeriod, bets}) => {
    console.log(bets)
    return (
        <div id="lk-mid">
            <div className="lk-table">
                <div className="lkt-filter">
                    <div className="lktf-bttns">
                        <div onClick={() => handleChangeType('bets')}
                             className="lktfb-one">
                        </div>
                        <div onClick={() => handleChangeType('operations')}
                             className="lktfb-one">
                        </div>
                    </div>
                    <div className="lktf-shedule">
                        <div className="lktfs-bttn">
                            <div className="global-ico gi-shedule"/>
                            <span>  </span>
                            <div className="global-ico gi-arrow-bot-g"/>
                        </div>
                    </div>
                </div>
                <div className="lkt-row lktr-title">
                    <div className="lktr-date"></div>
                    <div className="lktr-time"></div>
                    <div className="lktr-type"></div>
                    <div className="lktr-sum"></div>
                    <div className="lktr-result"></div>
                </div>
                <h1>[soon]</h1>
            </div>
        </div>
    )
}

const BetsScreen: FC<any> = (props: any) => {
    const {session} = useAppSelector(state => state.authReducer)
    const [bets, setBets] = useState([])
    const [period, setPeriod] = useState('all')
    const [type, setType] = useState('bets')

    useEffect(() => {
        const fetchBets = async () => {
            const {data} = await axios.post('http://gpbetapi.ru/bid_history', {
                period,
                user_id: session
            })
            setBets(data)
        }
        fetchBets()
    }, [period])

    const handleChangeType = (value: string) => {
        setType(value)
    }

    const handleChangePeriod = (value: string) => {
        setPeriod(value)
    }

    return (
        <div id="content-wr">
            <LeftBar active={type}/>
            {type === 'bets' && <Bets
                handleChangeType={handleChangeType}
                handleChangePeriod={handleChangePeriod}
                bets={bets}
                period={period}
            />}
            {type === 'operations' && <Operations
                handleChangeType={handleChangeType}
                handleChangePeriod={handleChangePeriod}
                bets={bets}
                period={period}
            />}
            <RightBar/>
        </div>
    );
};

export default BetsScreen;
